










import Record from 'caml-js/Record';
import dayOfYear from 'time-js/dayOfYear';
import {
    Component,
    Vue,
    Prop,
} from 'vue-property-decorator';
import {
    Observer,
} from 'mobx-vue';
@Observer
@Component
export default class App extends Vue {
    @Prop() readonly records!: Record[];
    @Prop() readonly year!: number;
    @Prop() readonly day!: number;

    get label() {
        let result = '';
        for(const record of this.records) {
            for(const data of record.userData) {
                if(data.key !== 'day label') {
                    continue;
                }
                result = data.value;
            }
        }
        return result;
    }

    get isToday() {
        const now = new Date();
        if(now.getFullYear() !== this.year) {
            return false;
        }
        const today = dayOfYear(
            now.getFullYear(),
            now.getMonth(),
            now.getDate(),
        );
        if(today !== this.day) {
            return false;
        }
        return true;
    }

}

